import styled from "styled-components";

export const RegistrationForm = styled.form`
  margin: 5rem;
  padding: 3rem;
  border: 1px solid black;
  text-align: center;

  @media only screen and (max-width: 900px) {
    margin: 1rem;
  }
`;



